background-color: rgba(0,0,0,0.41);} */
::selection{background: #1a75ff;color: #fff;} 
.login-back{overflow: hidden;max-width: 390px;background: #ffffff;padding: 1.4rem;
  border-radius: 15px;box-shadow: 0px 15px 20px rgba(0,0,0,0.1);}
.login-back .title-text{display: flex;width: 200%;position:relative;}
.login-back .title{width: 50%;font-size: 35px;font-weight: 600;
  text-align: center;transition: all 0.6s cubic-bezier(0.68,-0.55,0.265,1.55);}
.login-back .slide-controls{position: relative;display: flex;height: 50px;width: 100%;overflow: hidden;margin: 30px 0 10px 0;justify-content: space-between;
  border: 1px solid lightgrey;border-radius: 15px;}
.slide-controls .btn{height: 100%;width: 100%;color: #000000;font-size: 18px;
  font-weight: 500;text-align: center;line-height: 48px;cursor: pointer;
  z-index: 1;transition: all 0.6s ease;}
  .slide-controls .active{color:#ffffff;}

.slide-controls .slider-tab{position:absolute;height:100%;width:50%;left:0;z-index: 0;
  border-radius: 15px;background: -webkit-linear-gradient(left,#003366,#004080,#0059b3, #0073e6);transition: all 0.6s cubic-bezier(0.68,-0.55,0.265,1.55);}

  .slide-controls .slider-tab.active{
    left: 172px;
  }

.login-back .form-inner{display: flex;width: 200%; gap:2rem;position:relative;left:.6rem}
.login-back .active, .title-text.active {left:-22.05rem;}
.login-back .form-inner form{width: 50%;
    transition: all 0.6s cubic-bezier(0.68,-0.55,0.265,1.55);}
.form-inner form .field{height: 50px;width: 100%;margin-top: 20px;}
.form-inner form .field input{height: 100%;width: 100%;outline: none;padding-left: 15px;border-radius: 15px;border: 1px solid lightgrey;border-bottom-width: 2px;font-size: 17px;transition: all 0.3s ease;}
.form-inner form .field input:focus{  border-color:#1a75ff;}
.form-inner form .field input::placeholder{color:#999;transition:all 0.3s;}
form .field input:focus::placeholder{color: #1a75ff;}
.form-inner form .pass-link{margin-top: 5px;}
.form-inner form .signup-link{text-align: center;margin-top: 30px;}
.form-inner form .pass-link a,.form-inner form .signup-link a{color: #1a75ff;text-decoration: none;}
.form-inner form .pass-link a:hover,
.form-inner form .signup-link a:hover{text-decoration: underline;}
form .btn{height: 50px;width: 100%;border-radius: 15px;position: relative;overflow: hidden;}
form .btn .btn-layer{height: 100%;width: 300%;position: absolute;left: -100%;
  background: -webkit-linear-gradient(right,#003366,#004080,#0059b3,#0073e6);
  border-radius: 15px;transition: all 0.4s ease;}
form .btn:hover .btn-layer{  left: 0;}
form .btn input[type="submit"]{height:100%;width:100%;z-index:1;position:relative;background:none;border:none;color: #fff;padding-left: 0;
  border-radius: 15px;font-size: 20px;font-weight: 500;cursor: pointer;
}
.login_show{display: grid;}